.authorisation {
  width: 287px;
  height: 152px;
  margin: 22px 45px 0 0;
  padding: 20px 23px 20px 23px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f3f3f3;
  border-radius: 10px;
}

.authorisation__controls_block {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-top: 9px;
}

.authorisation__input {
  width: 238px;
  height: 41px;
  box-sizing: border-box;
  border: 1px solid #d4d4d4;
  border-radius: 7px;
  font-family: "Geneva", sans-serif;
  font-weight: 300;
  font-size: 13px;
}

.authorisation__input--email {
  margin-bottom: 8px;
  background-image: url("../icons/login-person.png");
  background-repeat: no-repeat;
  background-position: 7px 8px;
  padding-left: 30px;
  letter-spacing: 0.6px;
}

.authorisation__input--login {
  background-image: url("../icons/login-key.png");
  background-repeat: no-repeat;
  background-position: 9px 8px;
  padding-left: 32px;
  letter-spacing: 0.4px;
}

.authorisation__submit {
  width: 107px;
  height: 32px;
  margin-right: 2px;
  font-family: "Proxima Nova Semibold", sans-serif;
  font-weight: bold;
  font-size: 15px;
  color: #1b1a1a;
  background: linear-gradient(to bottom, #ffe665, #fbc356);
  border-radius: 4px;
}

.authorisation__links_wrapper {
  margin-left: 4px;
}

.authorisation__links {
  display: block;
  font-family: "Geneva", sans-serif;
  font-weight: 300;
  font-size: 10px;
  color: #2b2b2b;
  opacity: .4;
}

